/* Main Primary color */
/* Main Complement color */
.max-height {
  height: 100%;
}
.navbar-logo {
  padding: 0px 10px;
}
.bx-wrapper {
  width: 640px;
  margin: auto !important;
}
.bx-wrapper img {
  margin: 0 auto;
}
#page {
  float: right;
  height: 100%;
}
#page .panel {
  margin-bottom: 0px;
  min-height: 515px;
}
aside {
  float: left;
  position: absolute;
  top: 0px;
  bottom: 0px;
  background-color: #D56200;
}
aside .panel {
  margin: 0px;
}
.container {
  position: relative;
  height: 100%;
}
@media screen and (min-device-width: 1000px) {
  aside {
    width: 20%;
    margin: 1% 0.5% 1% 2.5%;
  }
  #page {
    width: 74%;
    margin: 1% 0.5% 1% 2.5%;
  }
}
